.close {
  width: 30px;
  height: 30px;
  display: flex;
  cursor: pointer;
  left: 15px;
}

.close::before,
.close::after {
  content: "";
  height: 6px;
  width: 29px;
  background-color: #fff;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -3px;
}

.close::before {
  transform: rotate(-45deg);
}

.close::after {
  transform: rotate(45deg);
}

.close:hover::before,
.close:hover::after {
  background-color: #d3d3d3;
}